"use client"

import { useQuery } from "@tanstack/react-query"
import { ArrowLeft, Truck, Package } from "lucide-react"
import { useRouter } from "next/navigation"
import { Card, Badge, SectionLoader, EmptyState } from "@/components/ui"
import { formatPaise, formatQuantity, DELIVERY_STATUS_LABELS } from "@/lib/constants"
import { formatDate } from "@/lib/utils"

export default function HistoryPage() {
  const router = useRouter()

  const { data, isLoading } = useQuery({
    queryKey: ["customer-profile"],
    queryFn: async () => {
      const res = await fetch("/api/customer/profile")
      if (!res.ok) throw new Error("Failed to fetch")
      return res.json()
    },
  })

  if (isLoading) return <SectionLoader />

  const deliveries = data?.data?.deliveries || []

  return (
    <div className="p-4">
      {/* Header */}
      <div className="flex items-center gap-4 mb-6">
        <button
          onClick={() => router.back()}
          className="h-10 w-10 flex items-center justify-center rounded-xl bg-neutral-100"
        >
          <ArrowLeft className="h-5 w-5" />
        </button>
        <h1 className="text-xl font-bold text-neutral-900">Delivery History</h1>
      </div>

      {/* Deliveries */}
      {deliveries.length === 0 ? (
        <EmptyState
          icon={Truck}
          title="No deliveries yet"
          description="Your delivery history will appear here once you start receiving milk."
        />
      ) : (
        <div className="space-y-3">
          {deliveries.map((delivery: any) => (
            <Card key={delivery.id}>
              <div className="flex items-start justify-between mb-3">
                <div>
                  <p className="font-semibold text-neutral-900">
                    {formatDate(delivery.deliveryDate)}
                  </p>
                  <p className="text-sm text-neutral-500">
                    {formatQuantity(delivery.quantityMl)}
                  </p>
                </div>
                <Badge
                  variant={
                    delivery.status === "DELIVERED"
                      ? "success"
                      : delivery.status === "PAUSED" || delivery.status === "HOLIDAY"
                      ? "warning"
                      : delivery.status === "BLOCKED" || delivery.status === "NOT_DELIVERED"
                      ? "error"
                      : "default"
                  }
                >
                  {DELIVERY_STATUS_LABELS[delivery.status as keyof typeof DELIVERY_STATUS_LABELS]}
                </Badge>
              </div>

              <div className="flex items-center gap-4 text-sm">
                <div className="flex items-center gap-2">
                  <Package className="h-4 w-4 text-neutral-400" />
                  <span className="text-neutral-600">
                    {delivery.largeBottles > 0 && `${delivery.largeBottles}×1L`}
                    {delivery.largeBottles > 0 && delivery.smallBottles > 0 && " + "}
                    {delivery.smallBottles > 0 && `${delivery.smallBottles}×500ml`}
                  </span>
                </div>
                
                {delivery.status === "DELIVERED" && delivery.chargePaise > 0 && (
                  <div className="ml-auto text-neutral-600">
                    {formatPaise(delivery.chargePaise)}
                  </div>
                )}
              </div>

              {delivery.status === "DELIVERED" && 
                (delivery.largeBottlesCollected > 0 || delivery.smallBottlesCollected > 0) && (
                <div className="mt-3 pt-3 border-t border-neutral-100">
                  <p className="text-sm text-green-600">
                    Bottles returned: 
                    {delivery.largeBottlesCollected > 0 && ` ${delivery.largeBottlesCollected}×1L`}
                    {delivery.smallBottlesCollected > 0 && ` ${delivery.smallBottlesCollected}×500ml`}
                  </p>
                </div>
              )}
            </Card>
          ))}
        </div>
      )}
    </div>
  )
}
